<template>
  <div class="home_container">
    <el-container>
      <el-header>
        <div class="left">
          <img src="../assets/heima.png" alt="" />
          <h1>电商后台管理系统</h1>
        </div>
        <el-button type="info" @click="loginout">退出</el-button>
      </el-header>
      <el-container>
        <el-aside :width="collapse ? '65px' : '200px'">
          <!-- 侧边菜单栏 -->
          <!-- 折叠 -->
          <div class="collapse" @click="collapse = !collapse">|||</div>
          <el-menu
            :collapse-transition="false"
            :collapse="collapse"
            :default-active="defaultActive"
            unique-opened
            class="el-menu-vertical-demo"
            background-color="#373d41"
            text-color="#fff"
            active-text-color="#ffd04b"
            @select="itenBtn"
            router
          >
            <el-submenu
              :index="item1.path"
              v-for="(item1, index1) in menusList"
              :key="index1"
            >
              <template slot="title">
                <i :class="classObj[item1.id]"></i>
                <span>{{ item1.authName }}</span>
              </template>
              <el-menu-item-group>
                <el-menu-item
                  :index="'/' + item2.path"
                  v-for="(item2, index2) in item1.children"
                  :key="index2"
                  @click="setname(item1.authName, item2.authName)"
                >
                  <i class="el-icon-menu"></i>
                  {{ item2.authName }}</el-menu-item
                >
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <div class="bread">
            <bread :guanli="guanli" :liebiao="liebiao"></bread>
          </div>
          <transition>
            <router-view></router-view>
          </transition>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import bread from './commonzujian/bread.vue'
export default {
  components: { bread },
  data () {
    return {
      guanli: '用户管理',
      liebiao: '用户列表',
      collapse: false,
      menusList: [],
      defaultActive: localStorage.getItem('defaultActive') || 'users',
      classObj: {
        125: 'iconfont icon-users',
        103: 'iconfont icon-tijikongjian',
        101: 'iconfont icon-shangpin',
        102: 'iconfont icon-danju',
        145: 'iconfont icon-baobiao'
      }
    }
  },
  created () {
    this.getmenu()
  },
  methods: {
    async getmenu () {
      const res = await this.$http.get('/menus')
      this.menusList = res.data.data
    },
    loginout () {
      this.$confirm('此操作将退出登录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '退出成功!'
          })
          // 退出成清除token并返回login页面
          localStorage.removeItem('token')
          this.$router.push('/login')
        })
        .catch(err => {
          err
        })
    },
    itenBtn (index, indexPath) {
      this.defaultActive = index
      localStorage.setItem('defaultActive', index)
    },
    // 面包屑
    setname (name1, name2) {
      // console.log(name1, name2)
      this.guanli = name1
      this.liebiao = name2
    }
  }
}
</script>
<style lang="less" scoped>
//动画
.v-enter .v-leave-to {
  transform: translateY(-100%);
}
.v-enter-active {
  transition: all 3s;
  animation: active 2s linear;
  opacity: 0;
}
.v-enter-to {
  opacity: 1;
  transform: translate(0);
}
@keyframes active {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translate(0);
  }
}

.home_container {
  width: 100%;
  height: 100%;
  .el-container {
    width: 100%;
    height: 100%;
  }
  .el-header {
    background-color: #373d41;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left {
      display: flex;
      align-items: center;
      width: 400px;
      h1 {
        display: flex;
        margin-left: 20px;
        color: #fff;
      }
    }
    .el-button {
      height: 40px;
    }
  }
  .el-aside {
    background-color: #373d41;
    .iconfont {
      margin-right: 20px;
    }
    .collapse {
      text-align: center;
      color: #fff;
      background-color: #4a5064;
      font-size: 12px;
    }
  }

  .el-main {
    background-color: #eaedf1;
    .bread {
      margin-bottom: 15px;
    }
  }
}
</style>
