<template>
  <div class="login">
    <div class="login_box" v-show="flag1">
      <div class="img">
        <img src="../assets/logo.png" alt="" />
      </div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm login_form"
      >
        <el-form-item prop="username">
          <el-input
            v-model="ruleForm.username"
            prefix-icon="iconfont icon-user"
            placeholder="请输入用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model="ruleForm.password"
            prefix-icon="iconfont icon-3702mima"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <el-form-item class="resct">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button @click="resetForm('ruleForm')" type="info">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 滑块 -->
    <slide-verify
      ref="slideblock"
      @success="onSuccess"
      @again="onAgain"
      @fulfilled="onFulfilled"
      @fail="onFail"
      @refresh="onRefresh"
      :w="352"
      :l="64"
      v-show="flag"
    ></slide-verify>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag1: true,
      flag: false,
      ruleForm: {
        username: "admin",
        password: "123456"
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 6, message: "长度在 3 到 6 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    login() {
      this.flag = true;
      this.flag1 = false;
    },
    onSuccess() {
      this.$refs.ruleForm.validate(async valid => {
        if (valid) {
          const res = await this.$http.post("login", this.ruleForm);
          window.localStorage.setItem("token", res.data.data.token);
          this.$router.push("/welcome");
        } else {
          return false;
        }
      });
    },
    onFail() {
      console.log("验证不通过");
      this.msg = "";
    },
    onRefresh() {
      console.log("点击了刷新小图标");
      this.msg = "";
    },
    onFulfilled() {
      console.log("刷新成功啦！");
    },
    onAgain() {
      console.log("检测到非人为操作的哦！");
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>
<style lang="less" scoped>
.login {
  height: 100%;
  width: 100%;
  background-color: #2b4b6b;
  position: relative;
  .login_box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 450px;
    height: 304px;
    background-color: #fff;
    img {
      position: absolute;
      left: 50%;
      width: 130px;
      height: 130px;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      border: 10px solid #fff;
      background-color: #ccc;
      box-shadow: 0 0 5px 2px #ddd;
    }
    .login_form {
      position: relative;
      padding: 90px 20px 0px;
      .resct {
        text-align: right;
      }
    }
  }
  .slide-verify {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
</style>
